import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
})
export class HomeComponent implements OnInit {
  title = 'Todo';
  loanapplication: Product[] = [];
  sel = 's';
  products: any[] = [
    { name: 'Personal Loan', id: 'personal' },
    { name: 'Car Loan', id: 'car' },
    { name: 'Home Loan', id: 'home' },
    { name: 'Mortgage Loan', id: 'mortgage' },
    { name: 'Gold Loan', id: 'gold' },
  ];

  @ViewChild('cardcontainer') cardcontainer!: ElementRef;

  env!: string;

  onSubmit(userForm: NgForm) {
    console.log(userForm.value);

    const loandetail = {} as Product;
    loandetail.name = userForm.value.name;
    loandetail.phone = userForm.value.phone;
    loandetail.amount = +userForm.value.amount;
    loandetail.facility = userForm.value.products;

    loandetail.theme = this.getTheme(loandetail.amount);
    loandetail.icon = this.getIcon(userForm.value.products);
    this.loanapplication.push(loandetail);

    userForm.form.reset();
  }

  getIcon(product: string): string {
    let icon = '';

    switch (product) {
      case 'home':
        icon = 'fa fa-home';
        break;

      case 'car':
        icon = 'fa fa-car';
        break;

      case 'mortgage':
        icon = 'fa fa-inr';
        break;

      case 'gold':
        icon = 'fa-solid fa-gem';

        break;

      case 'personal':
        icon = 'fa-solid fa-money-check-dollar-pen';
        break;
    }

    return icon;
  }
  getTheme(amount: number): string {
    let bgcolor = '';

    if (amount >= 1000000) {
      bgcolor = 'red';
    } else if (amount < 100000) {
      bgcolor = 'grey';
    } else if (amount >= 100000 && amount < 1000000) {
      bgcolor = 'green';
    }

    return bgcolor;
  }

  setTheme(loanamount: any) {}

  constructor() {}

  ngOnInit(): void {
    let s = navigator.userAgent;
    let windowspattern = /windows/gi;
    let androidpattern = /android/gi;
    let iphonepattern = /iphone/gi;
    const patternarray = [windowspattern, androidpattern, iphonepattern];
    patternarray.forEach((e, index) => {
      if (e.test(s)) {
        switch (index) {
          case 0:
            this.env = 'desktop';
            break;

          case 1:
            this.env = 'android';
            break;

          case 2:
            this.env = 'iphone';
            break;

          default:
            this.env = 'desktop';
            break;
        }
      }
    });
  }
}

export interface Product {
  name: string;
  amount: number;
  phone: string;
  facility: string;
  theme: string;
  icon: string;
}
